import React, { useState, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Image, Card, Accordion, Label, Icon, Segment } from 'semantic-ui-react'
import { getWeather } from '../reducers/weatherReducer'
import { getTimeZone } from '../reducers/timeZoneReducer'
import zoomLevel from '../zoomLevels'
import loadable from '@loadable/component'
import News from '../components/News'
const Map = loadable(() => import('../components/Map'))
const Weather = loadable(() => import('../components/WeatherMobile'))

const Country1 = ({ geo }) => {
  const [activeIndex, setActiveIndex] = useState(0)

  const dispatch = useDispatch()

  const country = useSelector((state) => state.countries.filtered[0])

  const weather = useSelector((state) => state.weather)
  const unit = useSelector((state) => state.unit.unit)

  const location = country.latlng

  useEffect(() => {
    dispatch(
      getWeather({
        location: location,
        unit: unit,
      })
    )
    dispatch(getTimeZone({ location: location }))
  }, [unit])

  const handleClick = (e, titleProps) => {
    const { index } = titleProps
    activeIndex === index ? setActiveIndex(-1) : setActiveIndex(index)
  }

  const weatherData = Object.entries(weather).length

  return weatherData < 1 ? (
    <>
      <Icon loading name="circle notch" />
    </>
  ) : (
    <>
      <Accordion fluid>
        {/* ------------------ Map -------------------- */}
        <Accordion.Title
          active={activeIndex === 0}
          index={0}
          onClick={handleClick}
        >
          <Icon name="dropdown" />
          Map
        </Accordion.Title>
        <Accordion.Content id="map-segment" active={activeIndex === 0}>
          <Segment>
            <Map
              lt={location[0]}
              lg={location[1]}
              zm={zoomLevel(country)}
              geo={geo}
            />
          </Segment>{' '}
        </Accordion.Content>
        {/* ------------------ News -------------------- */}
        <Accordion.Title
          active={activeIndex === 1}
          index={1}
          onClick={handleClick}
        >
          <Icon name="dropdown" />
          News
        </Accordion.Title>
        <Accordion.Content active={activeIndex === 1}>
          <Segment>
            <News />
          </Segment>{' '}
        </Accordion.Content>

        {/* ------------------ Flag -------------------- */}
        <Accordion.Title
          active={activeIndex === 2}
          index={2}
          onClick={handleClick}
        >
          <Icon name="dropdown" />
          Flag
        </Accordion.Title>
        <Accordion.Content active={activeIndex === 2}>
          <Image
            id="flag"
            centered
            src={country.flags.svg}
            alt="country flag"
            size="medium"
            bordered
          />
        </Accordion.Content>
        {/* ------------------ Coat of arms -------------------- */}
        <Accordion.Title
          active={activeIndex === 3}
          index={3}
          onClick={handleClick}
        >
          <Icon name="dropdown" />
          Coat Of Arms
        </Accordion.Title>
        <Accordion.Content active={activeIndex === 3}>
          <>
            {Object.entries(country.coatOfArms).length === 0 ? (
              <Label>No image available</Label>
            ) : (
              <Image
                id="coatOfArms"
                centered
                src={country.coatOfArms.svg}
                alt="coat of arms"
                size="medium"
                bordered
              />
            )}
          </>
        </Accordion.Content>
        {/* ------------------ Weather -------------------- */}
        <Accordion.Title
          active={activeIndex === 4}
          index={4}
          onClick={handleClick}
        >
          <Icon name="dropdown" />
          Weather
        </Accordion.Title>
        <Accordion.Content active={activeIndex === 4}>
          <Card id="CiaCard" fluid>
            <Weather country={country} />
          </Card>
        </Accordion.Content>
      </Accordion>
    </>
  )
}

const Country = React.memo(Country1)

export default Country
